import React from "react";

import Container from "react-bootstrap/Container";
import Badge from "react-bootstrap/Badge";
import "bootstrap/dist/css/bootstrap.min.css";
import { SummaryAnimation } from "../Animations.js";

const Summary = () => {
  return (
    <Container>
      <SummaryAnimation>
        <h1>Hey There,</h1>
        <div>I'm Emery, nice to meet you!</div>
        <p>Here's a little bit about me:</p>
        <div style={{ marginLeft: "1%" }}>
          <p>
          <span className="badge badge-danger" style={{ filter: "grayscale(20%)", backgroundColor: "#0099CC" }}>
            I worked as a:
          </span>
            {" Analyst Intern @ IHA, Tolko and Nokia "}
          </p>

          <p>
            <span className="badge badge-danger" style={{ filter: "grayscale(20%)", backgroundColor: "#e0305a" }}>
              I studied:
            </span>
              {" Bachelor of Information Engineering, South China University of Technology "}
          </p>

          <p>
           <span className="badge badge-danger" style={{ filter: "grayscale(20%)", backgroundColor: "#66CCFF" }}>
              Some of my skills are:
            </span>
            {" Power BI, SQL, Python, React"}
          </p>

          <p>
           <span className="badge badge-danger" style={{ filter: "grayscale(20%)", backgroundColor: "#996699" }}>
              I'm really into:
              </span>
            {" Coding, Puzzling, Anime Drawing, Blogging "}
          </p>
        </div>
        <p>
          I'm passionate about analyzing data to discover insights and coding projects. Check
          out my <a href="https://github.com/emeryli">Github</a> to see some of
          the projects that I've worked on!
        </p>
        <p>
          Currently seeking fulltime opportunities!
        </p>
        <p>
          If you want to chat, feel free to email me at{" "}
          <a href="mailto:helloemeryli@outlook.com">helloemeryli@outlook.com</a>,
          or reach out on{" "}
          <a href="https://www.linkedin.com/in/emeryli/">LinkedIn</a>!
        </p>
      </SummaryAnimation>
    </Container>
  );
};

export default Summary;